export default {
	data() {
		return {
			emailNum: '',
			password: '',
			password2:'',
			transactionPassword:'',
			transactionPassword2:'',
			inviteCode:'',
			opcity: 0.5,
			check:false,
			disabled:true,
			checkCode :''
		}
	},
	onLoad(options) {
		this.checkCode = options.checkCode 
		this.emailNum = options.emailNum
	},
	onReady() {

	},
	methods: {
		//设置密码
		register() {
			//注册
				uni.request({
					url: this.baseUrl + "/member-register",
					data: {
						Email: this.emailNum,
						Password : this.password,
						RepeatPassword:this.password2,
						PayPassword :this.transactionPassword,
						RepeatPayPassword:this.transactionPassword2,
						InviteCode:this.inviteCode,
						// InviteCode:280951,
						AuthCode:this.checkCode
						// AuthCode:101101
					},
					method: "POST",
					success: (res) => {
						console.log(res)
						if (res.data.status == 1) {
							uni.showToast({
								title: res.data.message,
								icon: 'none'
							})
							uni.navigateTo({
								url:"./login"
							})
						} else {
							uni.showToast({
								title: res.data.message,
								icon: 'none'
							})
						}
					}
				})
		},
		checked(){
			this.check =!this.check
			if (this.check == true) {
				this.opcity = 1
				this.disabled = false
			} else {
				this.opcity = 0.5
				this.disabled =true
			}
		}
	}
}
